___CSS_LOADER_EXPORT___.push([module.id, `/*Estilos Generales*/
body {
font-family: Arial, sans-serif;
background-color: #f4f4f4;
margin: 0;
padding: 0%;
height: 100vh ;
display: flex;
justify-content: center;
align-items: center;
}

/*Estilos para el contenedor prinncipal*/
#app{
   background-color: white; 
   padding: 20px;
   border-radius: 5px ;
   box-shadow: 0 0 10px rgb(0, 0 0 0.1);
}

/**Estilos para el formulario*/
form{
    display: flex;
    justify-content: space-between;
}

/*Estilos input*/
input{
    width: 75%;
    padding: 8px;
    margin-right: 10px;
    border: 1px solid #ddd;

}

/*Estilos al  boton*/
button{
    padding: 5px 8px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 3px;
    cursor: pointer;
}

/*Estilos a la lista*/
ul{
    list-style: none;
    padding: 0;
}

/*Estilos a los elementos de la lista*/
li{
    background-color: #f4f4f4;
    padding: 10px;
    border: 1px solid #ddd;
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
/*Estilos para cuando una tarea está completada*/
li.completed {
    text-decoration: line-through;
    color: #888;
    
